<template>
    <v-row no-gutters justify="center" class="mb-12">
        <v-col cols="12" xs="12" sm="12" md="8" lg="8">
            <v-breadcrumbs :items="items">
                <template v-slot:divider>
                    <v-icon>mdi-chevron-right</v-icon>
                </template>
            </v-breadcrumbs>
            <v-card outlined :tile="$vuetify.breakpoint.smAndDown ? true : false" class="text-center">

                <p class="mt-4 font-21 font-weight-bold">ແກ້ໄຂໂປຣຟຣາຍ</p>
                <v-avatar width="120" height="120">
                    <v-img
                           :src="base64 ? base64 : (  auth.image ? $server + auth.image : $profile ) "
                    >
                        <template v-slot:placeholder>
                            <v-row
                                    class="fill-height"
                                    align="center"
                                    justify="center"
                            >
                                <v-progress-circular size="60" indeterminate
                                                     color="primary"
                                ></v-progress-circular>
                            </v-row>
                        </template>
                    </v-img>
                </v-avatar>
                <v-col
                       xs="12" sm="12" md="10" lg="10"
                        class="mx-auto"
                >
                    <div>
                    </div>
                    <v-form @submit.prevent="updateProfile">
                        <div class="file-input">
                            <label class="v-btn info" for="file">
                                <i class="fas fa-upload fa-lg mr-2"></i>
                                ເລືອກຮູບໂປຣຟຣາຍ</label>
                            <input @change="imgUpload" type="file" id="file">
                        </div>
                        <v-text-field
                                v-model="auth.username"
                                label="username"
                                outlined
                                disabled
                        ></v-text-field>
                        <v-text-field
                                v-model="auth.name"
                                label="ຊື່"
                                outlined
                        ></v-text-field>
                        <v-text-field
                                v-model="auth.lastname"
                                label="ນາມສະກຸນ"
                                outlined
                        ></v-text-field>
                        <v-text-field
                                v-model="auth.email"
                                label="ອີເມວ"
                                disabled
                                outlined
                        ></v-text-field>
                        <v-text-field
                                v-model="auth.phone"
                                label="ເບີໂທລະສັບ"
                                outlined
                        ></v-text-field>
                        <!--                            <v-text-field-->
                        <!--                                    v-model="auth.facebookAccount"-->
                        <!--                                    label="facebook"-->
                        <!--                                    outlined-->
                        <!--                            ></v-text-field>-->
