                        <!--                                    label="ປະຫວັດການເຮັດວຽກ"-->
                        <!--                                    outlined-->
                        <!--                            ></v-text-field>-->
                        <v-btn block large type="submit" class="success"> ອັບເດດໂປຣຟຣາຍ </v-btn>
                    </v-form>
                </v-col>
                <br>
                <v-overlay :value="overlay">
                    <v-progress-circular color="primary" indeterminate size="70"></v-progress-circular>
                </v-overlay>
            </v-card>
        </v-col>
    </v-row>
</template>

<script>
    import { mapActions } from 'vuex'
    export default {
        props: [
            'auth'
        ],
        data: () => ({
            base64: '',
            file: '',
            overlay: false,
            items: [
                {
                    text: 'Dashboard',
                    to: { name: 'Dashboard' },
                    exact: true
                },
                {
                    text: 'Profile',
                }
            ],
        }),
        methods: {
            ...mapActions([
                'fetchAuth'
            ]),
            imgUpload(e) {
                const file = e.target.files[0]
                const reader = new FileReader() // Only read base64 not FormData
                if(file['size'] < 2000000) {
                    reader.onloadend = e => {
                        this.base64 = e.target.result
                    }
                    reader.readAsDataURL(file) // Base64 Reader
                    this.file = file // FormData Image
                }else {
                    this.$swal(
                        'Oops...',
                        'You are upload a large file',
                        'error'
                    )
                }
            },
            async updateProfile () {
                try {
                    this.overlay = true
                    const user = this.auth
                    const userdata = new FormData()
                    for(let key in user) {
                        userdata.append(key, user[key])
                    }
                    userdata.append('image', this.file)
                    await this.$axios.put('profile/update',  userdata )
                    await this.fetchAuth()
                    this.overlay = false
                    this.$router.push({ name: 'Dashboard'})
                    this.$swal({
                        title: 'ອັບເດດໂປຣໄຟຣແລ້ວ',
                        type: 'success',
                        toast: true,
                        position: 'top',
                        showConfirmButton: false,
                        timer: 2000,
                    })
                } catch (e) {
                    throw new Error(e)
                }
            },
        }
    }
</script>


